/* ==========================================================================
   UTILITY CLASSES
   ========================================================================== */

/*
   Container
   ========================================================================== */

.container {
  max-width: 1200px;
  padding-left: 1rem;
  padding-right: 1rem;

  @media(min-width: $small) {
    padding-left: 2rem;
    padding-right: 2rem;
  }

  @media(min-width: $medium) {
    padding-left: 3rem;
    padding-right: 3rem;
  }

  @media (min-width: $large) {
    margin-left: auto;
    margin-right: auto;
  }
}


/*
   Visibility
   ========================================================================== */

/* http://www.456bereastreet.com/archive/200711/screen_readers_sometimes_ignore_displaynone/ */
.hidden {
  display: none;
  visibility: hidden;
}

/* for preloading images */
.load {
  display: none;
}

.transparent {
  opacity: 0;
}

/* https://developer.yahoo.com/blogs/ydn/clip-hidden-content-better-accessibility-53456.html */
.visually-hidden,
.screen-reader-text,
.screen-reader-text span,
.screen-reader-shortcut {
  position: absolute !important;
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px !important;
  width: 1px !important;
  border: 0 !important;
  overflow: hidden;
}

body:hover .visually-hidden a,
body:hover .visually-hidden input,
body:hover .visually-hidden button {
  display: none !important;
}

/* screen readers */
.screen-reader-text:focus,
.screen-reader-shortcut:focus  {
  clip: auto !important;
  height: auto !important;
  width: auto !important;
  display: block;
  font-size: 1em;
  font-weight: bold;
  padding: 15px 23px 14px;
  background: #fff;
  z-index: 100000;
  text-decoration: none;
  box-shadow: 0 0 2px 2px rgba(0,0,0,.6);
}


/*
   Skip links
   ========================================================================== */

.skip-link {
  position: fixed;
  z-index: 20;
  margin: 0;
  font-family: $sans-serif;
  white-space: nowrap;
}

.skip-link li {
  height: 0;
  width: 0;
  list-style: none;
}

// Clear fixed positioned masthead
h1, h2, h3, h4, h5, h6 {

  &[id]:before {
    display: block;
    content: " ";
    margin-top: -75px;
    height: 75px;
    visibility: hidden;
  }
}


/*
   Type
   ========================================================================== */

/* center align */
.center {
  text-align: center;
}


/*
   Alignment
   ========================================================================== */

.float-right {
  clear: left;

  @include breakpoint($large) {
    float: right;
    clear: none;
  }
}

/* clearfix */
.cf { clear: both; }


/*
   Images
   ========================================================================== */

/* image align right */
.image-right {
  display: block;
  margin-left: auto;
  margin-right: auto;

  @include breakpoint($small) {
    float: right;
    margin-left: 1em;
  }
}


/*
   Icons
   ========================================================================== */

.icon {
  display: inline-block;
  fill: currentColor;
  width: 1em;
  height: 1.1em;
  line-height: 1;
  position: relative;
  top: -0.1em;
  vertical-align: middle;

  &.spin {
    animation: spin 2s infinite linear;
  }
}

.emoji {
  margin: 0;
  width: 1em;
  max-width: 20px;
  height: 1em;
  max-height: 20px;
}


/*
   Sticky, fixed to top content
   ========================================================================== */

.sticky {
  @include clearfix();
  position: -webkit-sticky;
  position: sticky;
  top: 80px;
}
